import { Avatar } from '@material-ui/core';
import React from 'react'

function TeamMembers({teamMembersList}) {
	function buildTeamMemberList() {
		return teamMembersList.map((teamMember) => {
			return (
				<div style={{display:"flex", padding:"5px"}}>
					<Avatar src={ teamMember.photo } />
					<h4 style={{ paddingLeft:"5px", paddingTop:"5px" }} >{ teamMember.displayName }</h4>
					<h4 style={{ paddingLeft:"5px", paddingTop:"5px" }} > - { teamMember.position }</h4>
				</div>
			);
		});
	};

  return (
    <div style={{
			height:"10vh",
			overflow:"scroll", 
			overflowX:"hidden",
		}}>
			{ buildTeamMemberList() }
		</div>
  );
};

export default TeamMembers;