import React from 'react';

import { Button } from '@material-ui/core';

function FutureLivestreamCard({futureLivestreamInfo}) {
	return (
		<div style={{ borderBottom:"3px solid grey", marginBottom:"5px", paddingLeft:"5px", textAlign:"left" }}>
			<div style={{ display:"flex" }}>
				<h5 style={{ marginRight:"10px" }}>Upcoming: { futureLivestreamInfo.title }</h5>
				<Button variant="contained" href="" style={{ backgroundColor:"lightblue" }}>
					RSVP
				</Button>
			</div>
			<h6>Event type: { futureLivestreamInfo.type }</h6>
			<div style={{ display:"flex" }}>
				<h6 style={{ marginRight:"10px" }}>Date: { futureLivestreamInfo.date }</h6>
				<h6>Time: { futureLivestreamInfo.time }</h6>
			</div>
		</div>
	);
};

export default FutureLivestreamCard;