import React, { useState } from 'react';
import Message from './Message.js';

import '../../style/Chat.css';

function Chat({ channelName, currentUser, initialMessages=[], userList }) {
  const [messages, setMessages] = useState(initialMessages);
  const [input, setInput] = useState("");

  const sendMessage = (e) => {
    e.preventDefault();
    const newMessage = {
      timestamp:new Date().toUTCString(),
      message:input,
      user:currentUser,
    }

    setMessages(...messages, newMessage);
    setInput("");
  }

  return (
    <div className="chat" style={{ height:"50vh", textAlign:"left" }}>
      <div style={{backgroundColor:"#363a3f", color: "white"}}>
        <h4 style={{paddingLeft:"5px"}}>{channelName}</h4>
      </div>
      <div className="chat_messages">
        {
          messages.map((message) =>{
            return <Message timestamp={ message.timestamp } message={ message.message } user={ message.user } />
          })
        }
      </div>
      <div className="chat_input">
        <form>
          <input 
            onChange={ (e) => setInput(e.target.value) }
            placeholder={ `Message #${channelName}` }
            value={ input }
          />
        </form>
        <button
          className="chat_input_button"
          type="submit"
          onClick={ sendMessage }
        >
          Send
        </button>
      </div>
    </div>
  )
}

export default Chat;