import React, { useState, useEffect } from 'react'
import TeamMembers from './TeamMembers';

import { Button, LinearProgress } from '@material-ui/core';

function WefunderRaiseInfo({teamMembers}) {

	const [amountRaised, setAmountRaised] = useState(0);

	useEffect(() => {
		const timer = setInterval(() =>{
			setAmountRaised((oldAmountRaised) => {
				if(oldAmountRaised === 100) {
					return 0;
				}
				const diff = Math.random() * 10;
				return Math.min(oldAmountRaised + diff, 100);
			})
		}, 500);

		return () => {
			clearInterval(timer);
		};
	}, []);

	return (
		<div className="container" 
			style={{ backgroundColor:"#363a3f", 
							borderBottom:"3px solid black", 
							color:"white", 
							height:"44.70vh", 
							overflowY:"scroll",
							paddingBottom:"5px",
							paddingLeft:"5px", 
							paddingRight:"5px", 
							textAlign:"left" }}>
			<h2>Company Name</h2>
			<p>This company does something revolutionary and you should invest in it!</p>
			<hr />
			<h2>Team Members</h2>
			<TeamMembers teamMembersList={ teamMembers }/>
			<hr />
			<h4 style={{color:"darkorange"}}>Early Bird Terms</h4>
			<LinearProgress variant="determinate" value={ amountRaised } />	
			<h6>${(Math.round(amountRaised) * 1000).toString()} / $100000</h6>
			<h6>Raised by { Math.round(amountRaised).toString() } investors</h6>
			<div style={{display:"flex"}}>
				<Button variant="contained" href="" style={{ backgroundColor:"royalblue", color:"white", marginRight:"10px"}}>
					$250 min - Invest
				</Button>
				<Button variant="contained" href="" style={{ backgroundColor:"royalblue", color:"white"}}>
					Watch for Updates
				</Button>
			</div>
		</div>
	);
};

export default WefunderRaiseInfo;