import React from 'react'
import CurrentLivestreamCard from './CurrentLivestreamCard';
import FutureLivestreamCardList from './FutureLivestreamCardList';

const testCurrentLivestreamInfo = {
	title:"Pitch Party",
	anticDuration:"1 hour"
};

const testFutureLivestreamInfo = [
	{
		title:"Future Show 1",
		type:"Pitch show",
		date:"March 1st",
		time:"1pm EST"
	},
	{
		title:"Future Show 2",
		type:"Podcast",
		date:"April 1st",
		time:"9am EST"
	},
	{
		title:"Investor AMA",
		type:"AMA",
		date:"April 20th",
		time:"4:20pm EST"
	}
]

function LivestreamCards() {
	function getFutureLivestreams() {
		// TODO build server endpoints to retrieve list of future livestreams
		return testFutureLivestreamInfo;
	}
	return (
		<div>
			<CurrentLivestreamCard currentLivestreamInfo={ testCurrentLivestreamInfo } />
			<FutureLivestreamCardList futureLivestreamCards={ getFutureLivestreams() } />
		</div>
	);
};

export default LivestreamCards;