import React from 'react'

import FutureLivestreamCard from './FutureLivestreamCard';

function FutureLivestreamCardList({futureLivestreamCards = []}) {
	return (
		<div>
			{ 
				futureLivestreamCards.map((futureLivestream) => {
					return (
						<FutureLivestreamCard futureLivestreamInfo={futureLivestream} />
					);
				}) 
			}
		</div>
	);
};

export default FutureLivestreamCardList;