import React from 'react'

function LivestreamContextBar({title, companyWebsite="example.com", companyLocation="Methuen, MA"}) {
	return (
		<div style={{backgroundColor:"#363a3f", color:"white", height:"14.70vh", paddingLeft:"5px", textAlign:"left"}}>
			<h4>{title}</h4>
			<div style={{display:"flex"}}>
				<a href="" style={{ color:"lightblue", paddingRight:"10px"}}>{ companyWebsite }</a>
				<p>{ companyLocation }</p>
			</div>
		</div>
	);
};

export default LivestreamContextBar;