import React, { useEffect, useState } from 'react';

import { FiberManualRecord } from '@material-ui/icons';

function CurrentLivestreamCard({currentLivestreamInfo}) {
	return (
		<div style={{ borderBottom:"3px solid grey", borderTop:"3px solid grey", marginBottom:"5px", paddingLeft:"5px", textAlign:"left" }}>
			<div style={{ display:"flex" }}>
				<h4 style={{ marginRight:"10px" }}>Currently Live: { currentLivestreamInfo.title }</h4>
				<FiberManualRecord style={{ color:"red", paddingTop:"5px" }}/>
			</div>
			<h6>Duration: { currentLivestreamInfo.anticDuration }</h6>
		</div>
	);
};

export default CurrentLivestreamCard;