import React from 'react'
import LivestreamContextBar from '../../components/LivestreamProper/LivestreamContextBar';
import LivestreamDocket from '../../components/LivestreamProper/LivestreamDocket';

function LivestreamWindow({currentLivestreamTitle}) {
    return (
        <div style={{overflowY:"scroll"}}>
            <LivestreamDocket />
            <LivestreamContextBar title={ currentLivestreamTitle } />
        </div>
    )
};

export default LivestreamWindow;